html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
}

/*        */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

/*        (ul) */
ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

/*        */
a {
  text-decoration: none;
  color: inherit;
}

/*      */
img {
  display: block;
  max-width: 100%;
  height: auto;
}

/*      address */
address {
  font-style: normal;
}
// 
.container {
  margin: 0 auto;
  padding: 0 20px;

  @media screen and (min-width: 320px) {
    width: 100%;
  }

  @media screen and (max-width: 767.9px) {
    max-width: 320px;
    padding: 0 20px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 32px;
  }

  @media screen and (min-width: 1280px) {
    padding: 0 32px;
    width: 1280px;
  }
}

//     
html,
body {
  height: 100%;
}
body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}
main {
  flex: 1 1 auto;
}
